import React, { useState, useEffect } from "react";
import ProductListItem from "./ProductListItem";
import { connect } from "react-redux";
import { getProduct } from "./servise";
import { addProduct } from "../Redux/product.action";
import "../assets/scss/App.scss";

function ProductList({ products, addProduct, statusButton }) {
  useEffect(async () => {
    addProduct(await getProduct());
  }, [statusButton]);

  return (
    <main className="productListContainer">
      <h4 className="productListTitle">All hot-dogs</h4>
      <ul className="productList">
        {products.map((product) => (
          <li key={product.id} className="productListItem">
            <ProductListItem product={product} />
          </li>
        ))}
      </ul>
    </main>
  );
}

const mapStateToProps = (state) => {
  return {
    products: state.productData.products,
    statusButton: state.productData.status,
  };
};

const mapDispatchToProps = {
  addProduct,
};

export default connect(mapStateToProps, mapDispatchToProps)(ProductList);
