import { createReducer } from "@reduxjs/toolkit";
import {
  addProduct,
  updateProduct,
  deleteProduct,
  addProductButton,
  editProductButton,
} from "./product.action.jsx";

const initialState = {
  products: [],
  status: {
    addProductButton: false,
    editProductButton: false,
  },
};

const productData = createReducer(initialState, {
  [addProduct]: (state, action) => ({
    products: [...action.payload],
    status: state.status,
  }),
  [updateProduct]: (state, action) => ({
    products: [
      ...state.products.map((product) =>
        product.id === action.payload.id ? action.payload : product,
      ),
    ],
    status: state.status,
  }),
  [deleteProduct]: (state, action) => ({
    products: [
      ...state.products.filter((product) => product.id !== action.payload),
    ],
    status: state.status,
  }),
  [addProductButton]: (state, action) => ({
    products: state.products,
    status: { ...state.status, addProductButton: action.payload },
  }),
  [editProductButton]: (state, action) => ({
    products: state.products,
    status: { ...state.status, editProductButton: action.payload },
  }),
});

export { productData };
