import axios from "axios";
axios.defaults.baseURL = "https://hot-dog-market.herokuapp.com/product/";
axios.defaults.headers.post["Content-Type"] = "application/json";

async function getProduct() {
  return await axios
    .get("/")
    .then((response) => {
      return response.data;
    })
    .catch((error) => console.log(error));
}

function addNewProduct(product) {
  axios
    .post("/add", product)
    .then()
    .catch((error) => console.log(error));
}

function upgrateProduct(product) {
  axios
    .post("/edit", product)
    .then()
    .catch((error) => console.log(error));
}

function deleteProduct(id) {
  axios
    .post(`/delete/${id}`)
    .then()
    .catch((error) => console.log(error));
}

export { getProduct, addNewProduct, upgrateProduct, deleteProduct };
